form {
  margin: 4rem 6rem;
  color: #2a2a2a;
}
input {
  width: 40%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
button[type="submit"] {
  width: 30%;
  background-color: #4caf50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.form {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}
.showDataStyle {
  display: flex;
}
.showDataStyle .showRow p {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
